
.tab-wrap {
  position: relative;
  z-index: 10;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-line-pack: distribute;
      align-content: space-around;
  border-bottom: 1px solid #eceeef;
}
.tab-wrap .tab-cell-text {
    display: block;
    position: relative;
}
.tab-wrap .tab-cell-text:before, .tab-wrap .tab-cell-text:after {
      content: "";
      display: inline-block;
      position: absolute;
      top: 50%;
}
.tab-wrap .tab-cell-text:after {
      width: 0;
      height: 0;
      border-top: 4px solid #666666;
      border-right: 4px solid transparent;
      border-left: 4px solid transparent;
      margin-top: -2px;
      margin-left: 5px;
}
.tab-wrap .tab-cell,
  .tab-wrap .tab-popup-cell {
    display: block;
}
.tab-wrap .popup-mask {
    display: none;
    position: fixed;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000)";
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
    background-color: rgba(0, 0, 0, 0.6);
}
.tab-wrap .tab-cell {
    -webkit-box-flex: 0;
        -ms-flex: 0 1 25%;
            flex: 0 1 25%;
    background-color: #ffffff;
    text-align: center;
    line-height: 3;
}
.tab-wrap .tab-cell + .tab-cell .tab-cell-text:before {
      left: 0;
      height: 1em;
      margin-top: -.5em;
      border-left: 1px solid #eceeef;
}
.tab-wrap .tab-cell.on .tab-cell-text {
      color: #fd5a37;
}
.tab-wrap .tab-cell.on .tab-cell-text:after {
        border-top: none;
        border-bottom: 4px solid #fd5a37;
}
.tab-wrap .tab-cell.on .tab-cell-popup-box {
      display: block;
}
.tab-wrap .tab-cell.on .popup-mask {
      display: block;
}
.tab-wrap .tab-cell-popup-box {
    display: none;
    position: absolute;
    left: 0;
    right: 0;
    top: 3em;
    z-index: 2;
    padding-bottom: 8px;
    text-align: left;
    background-color: #FFFFFF;
}
.tab-wrap .tab-popup-cell {
    padding-left: 20px;
    padding-right: 20px;
    position: relative;
}
.tab-wrap .tab-popup-cell:after {
      position: absolute;
      content: '';
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
      background-color: #dddddd;
}
.tab-wrap .tab-popup-cell.on {
      background-color: #eceeef;
      color: #fd5a37;
}
.tab-wrap .tab-popup-cell.on i {
        position: relative;
}
.tab-wrap .tab-popup-cell.on i:before {
          content: " ";
          top: 3px;
          position: absolute;
          width: 4px;
          height: 8px;
          border: 2px solid #fd5a37;
          border-left: 0;
          border-top: 0;
          -webkit-transform: rotate(45deg) scale(1);
                  transform: rotate(45deg) scale(1);
}



